// Structure de base d'une page.
*, ::before, :after {
    box-sizing: border-box;
}

:root{
    font-size:16px;
}
html, body {
    display: flex;
    flex-direction: column;
  }

body {
    margin: 0;
    padding:0 !important;
    font-size: 1em;
    color: var(--ul-color-black);
    background-color:var(--ul-color-white);
    font-family:var(--ul-font);
    }

main{
    flex:1;
    min-height:400px;
}

// Mixin gérant les largeurs maximales de contenu. Permet d'avoir un parent pleine largeur (par exemple une couleur d'arrière-plan)
@mixin ulContainer {
    margin:0 auto;
    width:100%;
    @media (min-width:$ul-paddingThreshold) {
        max-width:$ul-desktop-large-width;
      }
    }

// Mixin gérant le "coussin" entre le contenu et les limites du navigateur en fonction de la taille de ce dernier.
@mixin ulPaddings {
    padding-left:4rem;
    padding-right:4rem;
    @media (min-width:$ul-paddingThreshold) {
        padding-left:0;
        padding-right:0;
      }
    @media (max-width:$ul-tablet-width){
        padding-left:1.5rem;
        padding-right:1.5rem;
    }
    @media (max-width:$ul-mobile-width){
        padding-left:4rem;
        padding-right:4rem;
    }
    @media (max-width:$ul-tablet-small ){
        padding-left:1.5rem;
        padding-right:1.5rem;
    }
}

// Version spécifique au pied de page pour prendre en compte l'icône de carte
@mixin ulFooterPaddings {
    padding-right:4rem;
    padding-left:calc(4rem + 25px);
    @media (min-width:$ul-paddingThreshold) {
        padding-left:25px;
        padding-right:0;
        }
    @media (max-width:$ul-tablet-width){
        padding-right:1.5rem;
        padding-left:calc(1.5rem + 25px);
    }
    @media (max-width:$ul-mobile-width){
        padding-right:4rem;
        padding-left:calc(4rem + 25px);
    }
    @media (max-width:$ul-tablet-small ){
        padding-right:1.5rem;
        padding-left:calc(1.5rem + 25px);
    }
}

// Texte visible uniquement aux lecteurs d'écran, généralement utilisé pour le texte des icônes
.screen-reader-text {
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    -webkit-clip-path: inset(50%);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute !important;
    width: 1px;
    word-wrap: normal !important;
    word-break: normal;
}

//Bouton d'accessibilité pour passer directement au contenu principal de la page sans devoir faire le tour de la navigation
.skip-link:focus{
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgb(0 0 0 / 60%);
    clip: auto !important;
    -webkit-clip-path: none;
    clip-path: none;
    color: #21759b;
    display: block;
    font-size: 0.875rem;
    font-weight: 700;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
}